@charset "UTF-8";
@font-face {
  font-family: 'Proxima Nova';
  src: url("../fonts/proximaNova/ProximaNova-Regular.eot");
  src: url("../fonts/proximaNova/ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/proximaNova/ProximaNova-Regular.woff") format("woff"), url("../fonts/proximaNova/ProximaNova-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url("../fonts/proximaNova/ProximaNova-Semibold.eot");
  src: url("../fonts/proximaNova/ProximaNova-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/proximaNova/ProximaNova-Semibold.woff") format("woff"), url("../fonts/proximaNova/ProximaNova-Semibold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Billabong';
  font-style: normal;
  font-weight: normal;
  src: local("Billabong"), url("../fonts/billabong/Billabong.woff") format("woff"), url("../fonts/billabong/Billabong.woff") format("truetype");
}

html {
  height: 100%;
}

body {
  font-family: 'Proxima Nova', sans-serif;
  font-weight: 400;
  background: #fff;
  font-size: 16px;
  color: #000;
  line-height: 1.3;
  height: 100%;
}

.blocker {
  z-index: 1000 !important;
}

.container-fluid {
  display: flex;
  flex-direction: column;
  height: 100%;
}

input:focus,
input:active,
textarea:focus,
textarea:active,
button:focus,
button:active,
span:focus,
span:active,
button:focus,
button:active {
  outline: none;
}

select:focus,
select:hover,
option:hover,
option:focus {
  outline: none;
}

.preload {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100000000;
  background: #fff;
}

.offset-two-side {
  padding: 60px 0;
}

.title-s {
  text-align: center;
  font-size: 32px;
  color: #000;
  font-family: 'Lobster', cursive;
}

.logo-wrap .logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo-wrap .logo img {
  height: 40px;
}

.logo-wrap .logo .name-site {
  font-family: 'Billabong', sans-serif;
  padding-left: 15px;
  font-size: 34px;
  color: #000;
}

.direct {
  position: absolute;
  transform: rotate(-30deg);
  left: 55%;
  bottom: 12px;
  cursor: pointer;
  z-index: 1000;
}

.direct .zmdi {
  color: #fff;
  font-size: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .menu-toggle {
  margin-left: -21px;
}

.header .menu-toggle .ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.header .menu-toggle .hamRotate.active {
  transform: rotate(45deg);
}

.header .menu-toggle .hamRotate180.active {
  transform: rotate(180deg);
}

.header .menu-toggle .line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000;
  stroke-width: 5.5;
  stroke-linecap: round;
}

.header .menu-toggle .ham6 .top {
  stroke-dasharray: 40 172;
}

.header .menu-toggle .ham6 .middle {
  stroke-dasharray: 40 111;
}

.header .menu-toggle .ham6 .bottom {
  stroke-dasharray: 40 172;
}

.header .menu-toggle .ham6.active .top {
  stroke-dashoffset: -132px;
}

.header .menu-toggle .ham6.active .middle {
  stroke-dashoffset: -71px;
}

.header .menu-toggle .ham6.active .bottom {
  stroke-dashoffset: -132px;
}

.header .dropdown-menu {
  position: absolute;
  transform: translateX(-100%);
  opacity: 0;
  visibility: hidden;
  top: 75px;
  width: 400px;
  min-height: 400px;
  background: #fff;
  z-index: 990;
  transition: all .3s ease-in-out;
}

.header .dropdown-menu.active {
  transform: translateX(-15px);
  opacity: 1;
  visibility: visible;
}

.header .dropdown-menu ul {
  padding: 30px;
  display: flex;
  flex-direction: column;
  border-radius: 0 0 8px;
}

.header .dropdown-menu ul li {
  padding: 10px 0;
}

.header .dropdown-menu ul li a {
  text-decoration: none;
  font-size: 20px;
  color: #000;
}

.header .social-network ul {
  display: flex;
  justify-content: space-between;
}

.header .social-network ul li {
  margin-left: 20px;
}

.header .social-network ul li a {
  font-size: 20px;
  color: #000;
  transition: all .3s ease-in-out;
}

.header .social-network ul li a:hover {
  color: #007bff;
}

.content {
  flex: 1 0 auto;
}

div[data-story-id="case1"]:before {
  content: 'Монтаж вентиляции \A и кондиционирования (Москва)';
  position: absolute;
  white-space: pre;
  font-size: 14px;
  transform: rotate(30deg);
  z-index: 2;
  left: 45%;
  top: 17%;
}

div[data-story-id="case2"]:before {
  content: 'школа детского \A футбола (Казань)';
  position: absolute;
  font-size: 18px;
  transform: rotate(30deg);
  z-index: 2;
  left: 54%;
  font-weight: 600;
  top: 17%;
  color: #fff;
  white-space: pre;
}

div[data-story-id="case3"]:before {
  content: 'студия татуажа \A (Севастополь)';
  white-space: pre;
  position: absolute;
  font-size: 18px;
  padding: 25px;
  background: linear-gradient(5deg, #49ddd8 0%, #3369e7 100%);
  z-index: 2;
  border-radius: 9px;
  left: 54%;
  font-weight: 600;
  top: 17%;
  color: #fff;
}

div[data-story-id="case4"]:before {
  content: ' Квиз, плиз! \A (Новосибирск)';
  white-space: pre;
  position: absolute;
  font-size: 18px;
  padding: 25px;
  background: linear-gradient(135deg, #77e52d 0%, #2cc124 49%, #c2ce21 51%, #7db9e8 100%);
  border-radius: 9px;
  left: 54%;
  z-index: 2;
  font-weight: 600;
  top: 17%;
  color: #fff;
}

div[data-story-id="case5"]:before {
  content: ' Сеть кафе \A “Безопасный кекс” \A (Саратов)';
  white-space: pre;
  position: absolute;
  font-size: 18px;
  padding: 25px;
  background: linear-gradient(135deg, #77e52d 0%, #c2ce21 51%, #7db9e8 100%);
  border-radius: 9px;
  left: 54%;
  z-index: 2;
  font-weight: 600;
  top: 17%;
  color: #fff;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 0 0 auto;
  padding: 15px 0;
}

.footer p {
  line-height: 1.7;
  font-size: 16px;
}

.footer a {
  text-decoration: none;
  font-size: 16px;
  color: #000;
  transition: all .3s ease-in-out;
}

.footer a:hover {
  color: #007bff;
}

.footer .logo img {
  height: 40px;
}

.blocker {
  padding: 0 !important;
}

.modal {
  max-width: 100% !important;
  width: 100% !important;
  border-radius: 0 !important;
  height: 100vh;
  min-height: 400px;
}

.modal a.close-modal {
  top: 12.5px !important;
  right: 12.5px !important;
}

.modal .title-modal {
  font-size: 48px;
  color: #000;
  font-family: 'Lobster', cursive;
}

.modal p {
  padding-top: 20px;
  font-size: 16px;
}

.how-works {
  margin-top: calc(100vh - 237px);
}

@media only screen and (max-width: 1024px) {
  #zuck-modal-content .story-viewer .tip.link:before {
    top: -8px;
  }
  .how-works {
    margin-top: calc(100vh - 150px);
  }
}

@media only screen and (max-width: 767px) {
  .footer {
    flex-direction: column;
    align-items: center;
  }
  .footer .copyright, .footer .logo-wrap, .footer .contacts-wrap {
    padding: 20px 0;
    text-align: center;
  }
  .direct {
    left: 60%;
  }
}

@media only screen and (max-width: 576px) {
  .direct {
    left: 72%;
  }
  .header .dropdown-menu {
    width: 300px;
  }
}
